.photo-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.photoOftheDay {
    border: 2px solid rgb(132, 132, 132);
    border-radius: 5px;
    // width: 50vw;
}

.photo-line-1 {
    margin-top: 1.5%;
    display: flex;
    flex-direction: row;
    width: 150%;
    align-items: center;
    justify-content: center;
}

.photo-title {
    font-family: 'Kdam Thmor Pro', sans-serif;
    font-size: max(2.9vh, 12px);
}

.photo-more {
    font-size: 30px;
    margin-left: 1.5%;
}

.photo-copyright {
    font-family: 'Kdam Thmor Pro', sans-serif;
    color: rgb(165, 165, 165);
    font-size: max(1.8vh, 9px);
}

.photo-information {
    width: 40vw;
    font-size: .75vw;
    font-size: max(1.8vh, 9px);
}

.description-line {
    margin-top: 1%;
    margin-top: max(1%, 10px);
}


#container {
    position: relative;
    z-index: 1;
}

#draghandle {
    position: absolute;
    bottom: 0;
    right: 0;
    transform: translate(50%, 50%);
    z-index: 1;
}